body {
  margin: 0;
  padding: 0;
}

.Menu__link {
  display: none;
  font-family: "Geori", sans-serif; }
  @media all and (min-width: 768px) {
    .Menu__link {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column; } }
  @media all and (min-width: 1024px) {
    .Menu__link {
      -webkit-flex-direction: row;
              flex-direction: row;
      font-size: 1.2rem; } }

.Menu__link--item {
  text-decoration: none;
  color: #a2a2a2;
  margin: auto 10px; }

.Room__img {
  justify-self: center;
  height: 180px;
  transition: all 0.5s ease; }
  .Room__img:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    box-shadow: 0 0 5px 5px rgba(192, 188, 188, 0.5); }
  @media all and (min-width: 786px) {
    .Room__img {
      grid-row: 1/3;
      justify-self: flex-start; } }

.Room__description {
  text-align: start;
  font-family: "Roboto", "Oxygen", sans-serif;
  font-size: 0.9rem;
  margin: 0 20px; }
  @media all and (min-width: 786px) {
    .Room__description {
      grid-column: 2/4;
      font-size: 1.2rem;
      margin-top: 10px; } }

.Room__price {
  max-width: 90px;
  max-height: 30px;
  justify-self: end;
  color: #FDC103;
  background-color: #000;
  border-radius: 5px;
  padding: 2px 5px;
  margin: 10px 20px; }
  @media all and (min-width: 786px) {
    .Room__price {
      grid-column: 4/5;
      font-size: 1.2rem; } }

.Btn__container {
  text-align: center; }
  @media all and (min-width: 768px) {
    .Btn__container {
      text-align: end;
      grid-column: 3/5;
      margin: 10px 15px; } }
  @media all and (min-width: 1024px) {
    .Btn__container {
      -webkit-justify-content: space-between;
              justify-content: space-between; } }

.Btn {
  outline-style: none;
  color: #fff;
  -webkit-align-self: end;
          align-self: end;
  border: 0;
  padding: 5px;
  font-size: 1.2rem;
  font-family: "Roboto", "Oxygen", sans-serif;
  font-weight: 800;
  text-transform: uppercase; }
  @media all and (min-width: 1024px) {
    .Btn {
      font-size: 1.5rem; } }

.Btn__book {
  background-color: #3D8f31;
  border-bottom: 5px #146a20 solid; }
  @media all and (min-width: 1024px) {
    .Btn__book {
      margin-left: 20px; } }

.Btn__moreDetails {
  display: none;
  background-color: #f09218;
  border-bottom: 5px #c46e06 solid; }
  @media all and (min-width: 786px) {
    .Btn__moreDetails {
      display: inline-block; } }

.ResultList__container {
  height: 90vh;
  overflow-y: scroll;
  font-family: "Geori", sans-serif;
  font-size: 1.5rem;
  padding: 20px;
  border: 1px #146a20 solid;
  color: #3D8f31; }

.ResultList__container::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #F5F5F5; }

.ResultList__container::-webkit-scrollbar {
  width: 25px;
  background-color: #F5F5F5; }

.ResultList__container::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #3D8f31; }

.ResultList__item {
  display: grid;
  grid-template-rows: repeat(4, auto);
  grid-row-gap: 10px;
  margin: 20px 10px;
  padding: 20px 15px;
  list-style: none;
  border: 2px #3D8f31 solid;
  border-radius: 5px; }
  @media all and (min-width: 768px) {
    .ResultList__item {
      grid-template-rows: repeat(2, auto);
      grid-template-columns: 280px 1fr 1fr 1fr;
      grid-row-gap: 0px;
      padding: 25px; } }

.Wrap {
  margin: 20px; }
  @media all and (min-width: 1024px) {
    .Wrap {
      margin: 50px; } }

.Header__container {
  font-family: "Geori", sans-serif;
  color: #3D8f31;
  padding: 0 20px;
  border: 1px #146a20 solid;
  box-shadow: 2px #3D8f31; }
  @media all and (min-width: 768px) {
    .Header__container {
      font-size: 1.2rem;
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: space-between;
              justify-content: space-between;
      -webkit-align-items: center;
              align-items: center; } }
  @media all and (min-width: 1024px) {
    .Header__container {
      font-size: 1.5rem; } }

